/**
 * Custom debugger
 * @author foundy
 * @since 2017.08.04 - draft
 * @since 2017.08.31 - (bugfix) process detection
 */

'use strict';

/**
 * Module dependencies.
 */
const debug = require('debug');

/**
 * Module.exports
 */
module.exports = customDebug;

/**
 * Custom Debug
 *
 * @example const debug = require('customDebug')(__filename);
 *          debug('message...'); // {namespace} message...
 *          const debug = require('customDebug')(__filename, { verbose: true })
 *          debug('message...'); // {namespace:verbose} message...
 *          const debug = require('customDebug')(__filename, { verbose: true, suffix: 'function' })
 *          debug('message...'); // {namespace:function:verbose} message...
 *          const debug = require('customDebug')(__filename, { method: 'trace' })
 *          debug('message...'); // {namespace} message...  trace  (console.trace )
 *          const debug = require('customDebug')(__filename, { method: 'warn' })
 *          debug('message...'); // {namespace} message...  warn  (console.warn )
 * @param {string} namespace Debug namespace,  namespace   
 * @param {Object} [options={}] options
 * @param {number} [options.removeDepth=1] remove depth
 * @param {string} [options.suffix=''] namespace suffix
 * @param {boolean} [options.verbose=false] verbose 
 * @param {string} [options.method='log'] console method
 * @return {Function} debug module
 */
function customDebug(namespace, options = {}) {
  const {
    removeDepth = 1,
    suffix = '',
    verbose = false,
    method = 'log',
  } = options;

  // if js file path
  if (isJsFilePath(namespace)) {
    // remove root
    namespace = removeProcessCwd(namespace);

    // remove extension
    namespace = removeFileExtension(namespace);

    // replace '/' to ':'
    namespace = slashColon(namespace, removeDepth, suffix, verbose);
  }

  return createDebug(namespace, method);
}

/**
 * js    
 * @param {string} path  
 * @return {boolean} js   
 */
function isJsFilePath(path) {
  return /\.(js)$/.test(path);
}

/**
 * Node     
 * @param {string} path  
 * @return {string}   
 */
function removeProcessCwd(path) {
  if (typeof process === 'object' && typeof process.versions === 'object' && typeof process.versions.node !== 'undefined') {
    return path.replace(process.cwd() + '/', '');
  }

  return path;
}

/**
 *  
 *
 * @param {string} path  
 * @return {string}   
 */
function removeFileExtension(path) {
  return path.replace(/\..+$/, '');
}

/**
 * (/) (:) 
 *
 * @example slashColon('foo/bar/zoo', 0) // 'foo:bar:zoo'
 *          slashColon('foo/bar/zoo', 1) // 'bar:zoo'
 *          slashColon('foo/bar/zoo', 2) // 'zoo'
 *          slashColon('foo/bar/zoo', 1, 'suf') // 'bar:zoo:suf'
 *          slashColon('foo/bar/zoo', 1, '', true) // 'bar:zoo:verbose'
 * @param {string} path 
 * @param {number} removeDepth  depth
 * @param {string} suffix namespace suffix
 * @param {boolean} verbose verbose  
 * @return {string}    
 */
function slashColon(path, removeDepth, suffix, verbose) {
  const paths = path.split('/');
  let i = removeDepth;

  while (i > 0) {
    paths.shift();
    --i;
  }

  if (suffix) { paths.push(suffix); }
  if (verbose) { paths.push('verbose'); }

  return paths.join(':');
}

/**
 * Create debug
 *
 * @param {string} namespace Debug namespace
 * @param {string} method Console method,   console method
 * @return {Function} Debug
 */
function createDebug(namespace, method) {
  const log = debug(namespace);

  /* eslint-disable no-console, no-empty */
  // IE     try~catch 
  try {
    log.log = console[method].bind(console);
  } catch (e) {}
  /* eslint-enable no-console, no-empty */

  return log;
}


