/**
 * GV (Global Variables)
 * @description  Global          
 * @todo localStorage / Immutable / Library        
 * @author foundy
 * @since 2017.08.07
 */

'use strict';

/**
 * Module dependencies.
 * @description  vendor expose-loader require  global customDebug  
 * @todo vendor     require 
 */
const noop = () => {};
const customDebug = window['customDebug'];
const debug = customDebug ? customDebug(__filename) : noop;
const verbose = customDebug ? customDebug(__filename, { verbose: true, method: 'trace' }) : noop;

/**
 * Caching
 * @type {Object}
 */
const cache = {};

/**
 * Set variable
 *
 * @param {string} prop Property name
 * @param {*} value Property value
 * @return {Object} cache 
 */
exports.set = function set(prop, value) {
  cache[prop] = value;

  debug('GV::set() => prop: %s, value: %O', prop, value);
  verbose('GV::set() => prop: %s, value: %O', prop, value);

  return cache;
};

/**
 * Get variable
 *
 * @param {string} prop Property name
 * @return {*} prop   cache  , prop   Property value 
 */
exports.get = function get(prop) {
  if (!prop) {
    debug('GV::get() => prop: %s, return all: %O', prop, cache);
    verbose('GV::get() => prop: %s, return all: %O', prop, cache);
    return cache;
  }

  debug('GV::get() => prop: %s, return: %O', prop, cache[prop]);
  verbose('GV::get() => prop: %s, return: %O', prop, cache[prop]);

  return cache[prop];
};


